import React, { Component } from "react";
import PageHelmet from "../component/common/Helmet";
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/Header";
import ContactThree from '../elements/contact/ContactThree'

class PortfolioDetails1 extends Component{
    constructor () {
        super()
        this.state = {
          isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }
    openModal () {
        this.setState({isOpen: true})
    }
    render(){
        return(
            <React.Fragment>
                <PageHelmet pageTitle='Movie Search App' />

                <Header logoname="trydo-symbol-dark" />
                
                {/* Start Breadcrump Area */}
                <div className="rn-page-title-area pt--120 pb--190 bg_image bg_image--4"  data-black-overlay="7">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="rn-page-title text-center pt--100">
                                    <h2 className="title theme-gradient">Movie Search App</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Breadcrump Area */}

                {/* Start Portfolio Details */}
                <div className="rn-portfolio-details ptb--120 bg_color--1">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="portfolio-details">
                                    <div className="inner">
                                        <h2 className='yeet'>React App Development</h2>
                                        <p>For this project, my aim was to make an app where you can search for movies through an api. The api then return a title for the movie and some more information including an image/release date which is then displayed on the page.</p>
                                        <p>I used react to develop and build the site, to make sure the project was quick to load. The app is also mobile responseive and you can search for any movie in the entire database.</p>
                                        <p>You can visit the app here: <a target="_blank" rel="nofollow noopener noreferrer" href="https://sammer119.github.io/react-movie-search.github.io/"><u>React Movie Search</u></a></p>


                                    </div>
                                    <div className="portfolio-thumb-inner">
                                        
                                        <div className="thumb mb--30">
                                            <img src="/assets/images/portfolio/movie-search.png" alt="Portfolio Images"/>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Portfolio Details */}

                {/* Start Contact Area */}
                <div id="contact" className="fix">
                    <div className="rn ptb--120 bg_color--2">
                        <ContactThree contactImages="/assets/images/about/about-9.jpg" contactTitle="Contact Me..." />
                    </div>
                </div>
                {/* End COntact Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}


            </React.Fragment>
        )
    }
}
export default PortfolioDetails1;
