import React, { Component } from "react";
import PageHelmet from "../component/common/Helmet";
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/Header";
import ContactThree from '../elements/contact/ContactThree'

class PortfolioDetails0 extends Component{
    constructor () {
        super()
        this.state = {
          isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }
    openModal () {
        this.setState({isOpen: true})
    }
    render(){
        return(
            <React.Fragment>
                <PageHelmet pageTitle='Website Development' />

                <Header logoname="trydo-symbol-dark" />
                
                {/* Start Breadcrump Area */}
                <div className="rn-page-title-area pt--120 pb--190 bg_image bg_image--4"  data-black-overlay="7">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="rn-page-title text-center pt--100">
                                    <h2 className="title theme-gradient">ITWORX Website</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Breadcrump Area */}

                {/* Start Portfolio Details */}
                <div className="rn-portfolio-details ptb--120 bg_color--1">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="portfolio-details">
                                    <div className="inner">
                                        <h2>Website</h2>
                                        <h2 className='yeet'>Design and Development</h2>
                                        <p>After joining ITWORX I was tasked with redesigning and redeveloping the website. Once the project was complete, the new site led to an increase of 150% in site traffic.</p>
                                        <p>This project was a big challenge for me, as I was the only employee at the company with the skillset to develop and maintain it. This took careful planning and designing to make sure user experience (UX) was optimal over all platforms including mobile.</p>
                                        <p>I used bootstrap to style the site, my skills in HTML, CSS and JavaScript were paramount in the completion of the project. I used bootstrap to help with the styling and direction of the site. I also maintained a blog section for the website to keep customers up to date with the goings on of the company.</p>
                                        <p>You can visit the site here: <a target="_blank" rel="nofollow noopener noreferrer" href="https://itworxuk.com"><u>itworxuk.com</u></a></p>


                                    </div>
                                    <div className="portfolio-thumb-inner">
                                        
                                        <div className="thumb mb--30">
                                            <img src="/assets/images/portfolio/itworx-site.png" alt="Portfolio Images"/>
                                        </div>

                                        <div className="thumb mb--30">
                                            <img src="/assets/images/portfolio/itworx-footer.png" alt="Portfolio Images"/>
                                        </div>

                                        <div className="thumb mb--30">
                                            <img src="/assets/images/portfolio/blog.png" alt="Portfolio Images"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Portfolio Details */}

                {/* Start Contact Area */}
                <div id="contact" className="fix">
                    <div className="rn ptb--120 bg_color--2">
                        <ContactThree contactImages="/assets/images/about/about-9.jpg" contactTitle="Contact Me..." />
                    </div>
                </div>
                {/* End COntact Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}


            </React.Fragment>
        )
    }
}
export default PortfolioDetails0;
