import React, { Component } from "react";

class ContactThree extends Component{
    constructor(props){
        super(props);
        this.state = {
            rnName: '',
            rnEmail: '',
            rnSubject: '',
            rnMessage: '',
        };
    }
    render(){
        return(
            <div className="contact-form--1">
                <div className="container">
                    <div className="row row--35 align-items-start">
                        <div className="col-lg-6 order-2 order-lg-1">
                            <div className="section-title text-left mb--50">
                                <h2 className="title" style={{color: '#ffffff'}}>{this.props.contactTitle}</h2>
                                <p className="description" style={{color: 'rgba(198,201,216,0.75)'}}>I am seeking new work that will challenge me. Connect with me via phone: <a style={{color: '#ffffff'}} href="tel:07523 274114">07523 274114</a> or email:
                                    <a style={{color: '#ffffff'}} href="mailto:sam1atkins@yahoo.co.uk"> sam1atkins@yahoo.co.uk</a> </p>
                                <p className="description" style={{color: 'rgba(198,201,216,0.75)'}}>Thanks for taking a look at my portfolio.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default ContactThree;