import React, { Component } from "react";
import PageHelmet from "../component/common/Helmet";
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/Header";
import ContactThree from '../elements/contact/ContactThree'

class PortfolioDetails2 extends Component{
    constructor () {
        super()
        this.state = {
          isOpen: false
        }
        this.openModal = this.openModal.bind(this)
    }
    openModal () {
        this.setState({isOpen: true})
    }
    render(){
        return(
            <React.Fragment>
                <PageHelmet pageTitle='Google Keep Clone' />

                <Header logoname="trydo-symbol-dark" />
                
                {/* Start Breadcrump Area */}
                <div className="rn-page-title-area pt--120 pb--190 bg_image bg_image--4"  data-black-overlay="7">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="rn-page-title text-center pt--100">
                                    <h2 className="title theme-gradient">Google Keep Clone</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Breadcrump Area */}

                {/* Start Portfolio Details */}
                <div className="rn-portfolio-details ptb--120 bg_color--1">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="portfolio-details">
                                    <div className="inner">
                                        <h2>Website</h2>
                                        <h2 className='yeet'>Design and Development</h2>
                                        <p>I really enjoy using todo apps such as Google Keep. You can keep on top of the tasks that you have. There's always one you forget, so this takes all your tasks and puts them in one place.</p>
                                        <p>You can visit the site here: <a target="_blank" rel="nofollow noopener noreferrer" href="https://sammer119.github.io/google-keep-clone.github.io/"><u>Link</u></a></p>
                                    </div>
                                    <div className="portfolio-thumb-inner">
                                        <div className="thumb mb--30">
                                            <img src="/assets/images/portfolio/keep-clone.jpg" alt="Portfolio Images"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Portfolio Details */}

                {/* Start Contact Area */}
                <div id="contact" className="fix">
                    <div className="rn ptb--120 bg_color--2">
                        <ContactThree contactImages="/assets/images/about/about-9.jpg" contactTitle="Contact Me..." />
                    </div>
                </div>
                {/* End COntact Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}


            </React.Fragment>
        )
    }
}
export default PortfolioDetails2;
