import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

class TabsTwo extends Component{
    render(){
        let 
        tab1 = "Main skills",
        tab3 = "Experience",
        tab4 = "Education & Certification";
        const { tabStyle } = this.props
        return(
            <div>
                {/* Start Tabs Area */}
                <div className="tabs-area">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <Tabs>
                                    <TabList  className={`${tabStyle}`}>
                                        <Tab>{tab1}</Tab>

                                        <Tab>{tab3}</Tab>
                                        <Tab>{tab4}</Tab>
                                    </TabList>

                                    <TabPanel>
                                        <div className="single-tab-content">
                                            <ul>
                                                <li>
                                                    <a href="/service">Website Design and Development<span></span></a>
                                                    Being able to bring design to life and seeing an end product of a website/app gives me great joy.
                                                </li>
                                                <li>
                                                    <a href="/service">User experience design <span> - UI/UX</span></a>
                                                    People try to make interactions with the outside world as seamless as possible (sometimes), why not take it into the online world.
                                                </li>
                                            </ul>
                                        </div>
                                    </TabPanel>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                           <ul>
                                                <li>
                                                    <a href="/service">Business Support Coordinator<span> - IT-Worx Solutions</span></a> Feb 2019 - Present
                                                </li>
                                                <li>
                                                    <a href="/service">Sales Advisor<span> - New Look</span></a> 2017 - 2019
                                                </li>
                                           </ul>
                                       </div>
                                    </TabPanel>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                            <ul>
                                                <li>
                                                    <a href="/service">BSc In Computing Graphic and Animation<span> - Robert Gordon University, Aberdeen</span></a> 2017
                                                </li>
                                                <li>
                                                    <a href="/service">Diploma in Computer Science<span> - Gamma Technical Institute</span></a> 2009
                                                </li>
                                            </ul>
                                       </div>
                                    </TabPanel>
                                    
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Tabs Area */}
            </div>
        )
    }
}



export default TabsTwo;